.post {
    width: 100%;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 17px -8px rgba(0, 0, 0, 0.7);
    box-shadow: 0px 0px 17px -8px rgba(0, 0, 0, 0.7);
    margin: 30px 0;
}

.postWrapper {
    padding: 10px;

}

.postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postTopLeft {
    display: flex;
    align-items: center;
}

.postProfileImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}

.postUserName {
    font-size: 16px;
    font-weight: bold;
    margin: 0 10px;
}

.postDate {
    font-size: small;
}

.postText {
    margin-left: 14px;
}

.postCenter {
    margin: 20px 0;
}

.postImg {
    margin-top: 20px;
    width: 100%;
    max-height: 500px;
    object-fit: contain;
    border-radius: 6px;
}

.postBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postBottomLeft {
    display: flex;
    align-items: center;
}

.likeIcon {
    width: 18px;
    height: 18px;
    margin-right: 5px;
    cursor: pointer;
}

.postLikeCounter {
    font-size: 14px;
}

.postCommentText {
    cursor: pointer;
    border-bottom: 1px dashed gray;
}